<template>
	<div class="contents">
		<div class="form-wrapper form-wrapper-sm">
			<form class="form">
				<div>
					<label for="userId">id :</label>
					<input type="text" id="userId" v-model="userId" ref="userId" @keyup.enter="login" />
				</div>
				<div>
					<label for="password">pw : </label>
					<input type="password" id="password" v-model="password" ref="password" @keyup.enter="login" />
				</div>
				<button type="button" @click="login" class="btn">
					로그인
				</button>
				<p class="log">{{ logMessage }}</p>
			</form>
		</div>
	</div>
</template>

<script>
import { validateEmail } from '@/utils/validation';
import CommonMixin from '@/mixins/commonMixin';
import { mapActions } from 'vuex';

export default {
	mixins: [CommonMixin],
	data() {
		return {
			userId: '',
			password: '',
			logMessage: '',
		};
	},
	computed: {
		isIdValid() {
			return validateEmail(this.userId);
		},
	},
	methods: {
		...mapActions(['LOGIN']),
		async login() {
			// if (!(await this.sConfirm('등록하시겠습니까?'))) {
			// 	return;
			// }
			if (!this.userId) {
				this.sAlert('알림', '아이디를 입력해 주세요', '확인');
				this.$refs.userId.focus();
				return;
			} else if (!this.password) {
				this.sAlert('비밀번호를 입력해 주세요');
				this.$refs.password.focus();
				return;
			}
			const params = {
				userId: this.userId,
				password: this.password,
			};
			try {
				let res = await this.LOGIN(params);
				if (res.result == 0) {
					this.$router.push('/flexible/workPlan');
				} else {
					this.sAlert(res.resultMsg);
				}
			} catch (e) {
				console.log(e);
				// this.logMessage = e.response.data.resultMsg;
			}
		},
	},
};
</script>

<style scoped>
.btn {
	color: white !important;
}
/*--- FORM ---*/
.form-container {
	display: flex;
	align-items: center;
}
.form-wrapper {
	background: white;
	-webkit-box-shadow: 0 20px 20px rgba(0, 0, 0, 0.08);
	box-shadow: 0 20px 20px rgba(0, 0, 0, 0.08);
	border-radius: 3px;
	padding: 15px 15px;
}
.form-wrapper.form-wrapper-sm {
	max-width: 500px;
	margin: 40px auto;
}
.form-wrapper-sm .page-header {
	padding: 0px 0 20px;
}
.form {
	width: 460px;
	height: 100%;
}
.form .validation-text {
	margin-top: -0.5rem;
	margin-bottom: 0.5rem;
	font-size: 1rem;
	display: flex;
	flex-direction: row-reverse;
	justify-content: space-between;
}
.form label {
	width: 100%;
	display: block;
	margin-bottom: 0.5rem;
	color: #364f6b;
	font-size: 90%;
}
.form input,
.form textarea {
	font-family: inherit;
	font-size: 100%;
	width: 100%;
	border: 1px solid #dae1e7;
	box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
	padding: 0.5rem 0.75rem;
	margin-bottom: 1rem;
}
.form input.valid {
	border: 1px solid #21b314;
}
.form input.invalid {
	border: 1px solid red;
}
.form div:nth-last-child(2) {
	margin-bottom: 0.5rem;
}
.btn {
	background: #fe9616;
	padding: 0.5rem 1.5rem;
	font-weight: 700;
	border-radius: 0.25rem;
	border: 0 solid #dae1e7;
}
.btn.disabled {
	opacity: 0.5;
	cursor: not-allowed;
}
</style>
